const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: [],
			imgUrl: '',
			page: 1,
			token: ''
		};
	},
	onLoad() {
		this.token = uni.getStorageSync('token')
		// 获取收藏列表
		this.getList()
		this.imgUrl = helper.imgUrl
	},
	methods:{
		deleteItem(e) {
			// 点击删除
			uni.showModal({
				title: '提示',
				content: '确认删除?',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						let index = e.currentTarget.dataset.index
						this.runDeleteItem(index)
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		runDeleteItem(index) {
			// 执行草稿删除
			uni.showLoading({
			  title: '删除中...'
			});
			let list = this.list
			uni.request({
				url: `${helper.requestUrl}/posts/del`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					post_id: list[index].post_id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message,
							icon: 'success',
							duration: 2000
						});
						let newList = []
						for (let i = 0; i < list.length; i++) {
							if (list[i].post_id != list[index].post_id) {
								newList.push(list[i])
							}
						}
						this.list = newList
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		getList() {
			// 获取收藏列表
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/publish`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data:{
					page_size: '20',
					page: this.page,
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						this.list = this.list.concat(res.data.data)
					} else {
						uni.showToast({
							title: res.data.message,
							icon:'none'
						});
					}
			
				}
			})
		},
		geDetail(e){
			console.log(e)
			uni.navigateTo({
				url:`/pages/articleDetail?id=${e.currentTarget.dataset.id}`
			})
		},
		onReachBottom() {
			this.page ++;
			this.getList()
		}
	}
}
